.skills-section {
  .working-history {
    padding: 0 60px;
  }

  .vertical-timeline-element-content {
    width: 100%;
    
    .working-history-skills {
      margin-top: 10px;
      display: flex;
      flex-wrap: wrap;
    }

    img {
      height: 35px;
    }

    .skill-pill {
      display: inline-block;
      background: #F4DF21;
      border-color: #E4D018;
      border-radius: 20px;
      padding: 3px 10px;
      margin-right: 5px;
      font-size: 13px;
      margin-bottom: 8px;
      text-transform: capitalize;
    }
  }

  .load-more-timeline-events {
    padding: 15px 20px;
    width: 110px;
    font-size: 12px;
    margin: 0 -35px;
  }
}