$green: "#07c196";

html {
  scroll-behavior: smooth
}

.width50 {
  width: 50%;
}

.hello-welcome {
  .welcome {
    margin-top: -60px;
    background-image: url("/hello/biteable/background.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    nav.bg-transaprent {
      padding: 10px 80px 15px 80px;
      background-color: rgba(0,0,0,.2) !important;
      color: white;
      padding-top: 20px;

      .nav-link {
        color: white;
        font-weight: 500;
        text-transform: uppercase;
        font-size: .8rem;

        &:hover {
          text-decoration: underline;
        }
      }

      svg {
        width: 120px;
      }
      
      h1 {
        font-size: 1.7rem;
        margin-bottom: 0;
        display: flex;
        align-items: flex-end;
      }
      
      .btn-white {
        color: #2eb798;
        border-color: #2eb798;
        border-width: 2px;
        background: rgba(255,254,255,.6);
        transition: background-color 1s;
        
        &:hover {
          transition: background-color 1s;
          background-color: rgba(255, 254, 255, .9);
          border-color: #07c196;
        }
      }
      
      .download-resume-in-nav {
        display: flex;
        flex-grow: 1;
        justify-content: flex-end;
      }
    }

    .content-dark-background {
      color: white;
      background: rgba(0,0,0,.2);

      .content-dark-background-container {
        flex-grow: 1;
      }

      .down-arrow-section {
        flex-basis: 0;
      }
  
  
    }
    
    .content {
      // position: absolute;
      display: flex;
      flex-direction: row;
      width: 100%;
      height: 100%;

      .height100 {
        height: 100%;
      }
      
      .introText {
        padding-left: 80px;
        height: 100%;
        display: flex;
        width: 50%;
        color: white;
        font-size: 20px;
        text-align: right;
        align-items: center;

        h3 {
          font-size: 40px;
          font-weight: 600;
        }
    
        h4 {
          font-size: 20px;
          padding-bottom: 40px;
        }
    
        p {
          color: white;
          font-size: 25px;
          line-height: 1.3;
        }
      }
      
      .introProfilePicture {
        width: 50%;
        display: flex;

        .halo {
          border: 5px solid #d4d4d4;
          border-radius: 50%;
        }
          
        .round-clipper {
          width: 250px;
          height: 250px;
          overflow: hidden;
          border-radius: 50%;
          border: 5px solid transparent;
          z-index: 10;
          display: flex;
          flex-direction: column;

          &:hover {
            .play {
              display: block;
              animation: fadeInFromNone 0.25s ease-in-out;
            }
          }

          .play { 
            position: relative;
            color: #07c196;
            bottom: 195px;
            left: 90px;
            font-size: 80px;
            z-index: 9999;
            width: 100px;
            height: auto;
            display: none;
          }
        }
      }

      .downArrow {
        color: white;
        font-size: 50px;
        padding-bottom: 50px;
      }
    }
  }

  .video-section {
    color: black;
    padding: 50px;
    display: flex;
    justify-content: center;

    .video-container {
      padding-top: 20px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
      background-image: url(https://i.dlpng.com/static/png/6985288_preview.png);
    }

    h2 {
      text-align: center;
    }

    .video-text {
      justify-content: flex-end;
      padding-right: 100px;
      align-items: center;
    }
    
    .video {
      display: flex;
      justify-content: center;
      border-radius: 5px;
      border: 10px solid #383838;
      margin-bottom: 5px;
    }
    
    .halo {
      display: flex;
      justify-content: center;  
    }

    hr {
      margin-top: 70px;
    }
  }

  .contact-us-section {
    .contact-information {
      display: flex;
      flex-direction: column;
      align-items: center;

      h4 {
        font-size: 1.5rem;
        font-weight: 400;
        margin-bottom: 30px;
      }

      p {
        font-size: 1.3rem;
        margin-bottom: 30px;
        
        > i {
          color: #666;
          padding-right: 20px;
        }

        a i {
          font-size: 30px;
          padding-right: 8px;
        }
      }
    }
  }

  .skills-section {
 
    h2 {
      text-align: center;
      padding-bottom: 25px;
    }


    .skills-radar {
      margin-top: 30px;
    }
    
    hr {
      margin-top: 70px;
    }

  }
}














@-webkit-keyframes fadeInFromNone {
  0% {
      display: none;
      opacity: 0;
  }

  1% {
      display: block;
      opacity: 0;
  }

  100% {
      display: block;
      opacity: 1;
  }
}

@-moz-keyframes fadeInFromNone {
  0% {
      display: none;
      opacity: 0;
  }

  1% {
      display: block;
      opacity: 0;
  }

  100% {
      display: block;
      opacity: 1;
  }
}

@-o-keyframes fadeInFromNone {
  0% {
      display: none;
      opacity: 0;
  }

  1% {
      display: block;
      opacity: 0;
  }

  100% {
      display: block;
      opacity: 1;
  }
}

@keyframes fadeInFromNone {
  0% {
      display: none;
      opacity: 0;
  }

  1% {
      display: block;
      opacity: 0;
  }

  100% {
      display: block;
      opacity: 1;
  }
}




/* Landscape phones and down */
@media (max-width: 480px) { 
  .navbar {
    justify-content: center;

    .download-resume-in-nav {
      justify-content: center !important;
    }
  }
  
  .hello-welcome {
    .welcome {
      .content {

        .content-dark-background-container {
          flex-grow: 0;
        }
        .introText {
          text-align: center;
          padding-left: 0;
          padding-right: 0;
        }

        .introProfilePicture {
          display: none;
        }
      }
    }

    .skills-section {
      
      .skills-radar {
        padding: 0 30px;
      }

      .working-history {
        padding: 0 60px 0 5px;
      }
    }    
  }
}
